@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  font-family: "Inter";
}

html {
  scroll-behavior: smooth;
}

body {
  background: linear-gradient(135deg, rgba(0,71,62,1) 0%, rgba(0,68,2,1) 100%);
  color: white;
  margin: 0;
}

.viewArea {
  max-width: 1150px;
	margin: 0 auto !important;
	float: none !important;
  padding: 0em 1em;
}

h1 {
  font-weight: 900;
}

a {
  color: #CCCCCC;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.flex-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: 2em;
  gap: 2em;
}

.flex-container img {
  max-width: 575px;
  height: 100%;
  max-height: 80vh;
  transition: 0.5s all ease-in-out;
}

.flex-container .imageBox {
  overflow: hidden;
  display: flex;
  -webkit-filter: drop-shadow(0 0 0.25em black);
          filter: drop-shadow(0 0 0.25em black);
  border-radius: 10px;
  transition: 0.5s all ease-in-out;
  margin: 0;
}

.flex-container .imageBox:hover {
  -webkit-filter: drop-shadow(0 0 0.5em black);
          filter: drop-shadow(0 0 0.5em black);
}

.flex-container img:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.linkButton {
  padding: 1.5em 1em;
  margin: 0.25em 0em;
  background-color: rgba(255,255,255,0.2);;
  color: white;
  text-decoration: none;
  text-align: center;
  display: inline-block;
  transition: background-color 0.25s, color 0.25s;
  border-radius: 128px;
}

.linkButton:hover {
  text-decoration: none;
  background-color: white;
  color: black;
}

.linkButton:active {
  text-decoration: none;
  background-color: rgba(255,255,255,0.6);
  color: black;
}

.buttonWrapper {
  display: flex;
  justify-content: space-around;  
}

.sectionHeader {
  font-size: 3.5em;
  margin: 0;
}

.outerContact {
  background-image: url(/home/static/media/code_pic1.e275c1ec.jpg);
}

@media screen and (max-width: 700px) {
  .viewArea {
    max-width: 500px;
  }

  .flex-container {
    flex-direction: column;
  }

  .flex-container img {
    max-width: 100%;
    max-height: 100vh;
  }

  .sectionHeader {
    text-align: center;
  }
}
.Navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 5em;
  font-weight: 500;
}
  
.Navbar h1 {
  font-weight: 700;
  text-align: center;
}
  
.Navbar a {
  color: white;
  text-decoration: none;
}
  
.Navbar .Links {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  display: flex;
}
  
.Navbar .Links li {
  float: right;
}
  
.Navbar .Links li a {
  display: block;
  text-align: center;
  padding: 14px 16px;
  transition: 0.25s all ease-in-out;
  border-radius: 32px;
}
  
.Navbar .Links li a:hover {
  background-color: rgba(0,0,0,0.3);
}
  
.Navbar .Links li a:active {
  background-color: rgba(0,0,0,0.5);
}

@media screen and (max-width: 700px) {
  .Navbar {
    flex-direction: column;
    height: auto;
  }

  .Navbar .Links li {
    float: left;
  }
}
.About .flex-container {
    min-height: calc(100vh - 7em);
    padding: 1em 0em;
}

.About .flex-container img {
  height: 100%
}

@media screen and (max-width: 700px) {
  .About .flex-container {
    flex-direction: column-reverse;
  }
}
.Example h1 {
  font-weight: 700;
}

.Example h2 {
  font-weight: 500;
}

.Example img {
  -webkit-filter: brightness(1) blur(0px);
          filter: brightness(1) blur(0px);
  transition: 0.5s all ease-in-out;
  min-width: 50%;
  height: auto;
}

.Example img:hover {
  -webkit-filter: brightness(1.25);
          filter: brightness(1.25);
}

.Example img:active {
  -webkit-filter: brightness(0.5) blur(16px);
          filter: brightness(0.5) blur(16px);
  transition: 0.1s -webkit-filter ease-in-out;
  transition: 0.1s filter ease-in-out;
  transition: 0.1s filter ease-in-out, 0.1s -webkit-filter ease-in-out;
}
#works {
  padding: 0;
}

.outerWorks {
  min-height: calc(100vh - 2em);
  padding: 1em 1em;
  display: flex;
  align-items: center;
  background-color: rgba(0,0,0,0.3);
}
.outerContact {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.contactBackground {
  background-color: rgba(0,0,0,0.5);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
}

.Contact .flex-container {
  min-height: calc(100vh - 6em);
  padding: 1em 0em;
  width: 100%;
}

#emailCopier {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 1em;
  border-radius: 64px;
  background-color: rgba(255,255,255,0.2);
  color: white;
  transition: background-color 0.25s;
}

#emailCopier:hover {
  background-color: white;
}

#emailCopier:hover #emailCopyBox {
  color: black;
}

#emailCopier:hover #emailCopyButton i {
  color: black;
}

#emailCopier:active #emailCopyBox {
  color: black;
}

#emailCopier:active #emailCopyButton i {
  color: black;
}

#emailCopier:active {
  background-color: rgba(255,255,255,0.6);
}

#emailCopyBox {
  line-height: 5.5em;
  color: white;
  box-shadow: none;
  border-width: 0px;
  padding: 0em 1em;
  width: 10em;
  background-color: transparent;
  border-radius: 64px;
  transition: background-color 0.25s, color 0.25s;
}

#emailCopyBox:hover {
  outline:none!important;
  background-color: white;
  color: black;
}

#emailCopyBox:focus {
  outline:none!important;
  background-color: white;
  color: black;
}

#emailCopyButton {
  padding: 10px;
  color: white;
  border-radius: 256px;
}

#emailCopyButton i {
  transition: color 0.25s;
}

#contactBox {
  flex-grow: 2;
}

#contactLinks {
  display: flex;
  flex-direction: column;
  justify-content: space-between  ;
}

#contactLinks .buttonWrapper {
  flex-wrap: wrap;
}

#emailBox {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 1.5em;
  grid-gap: 1em;
  gap: 1em;
}

#emailButton {
  min-width: 10em;
  padding: 1.25em 0em;
}

#resumeButton {
  min-width: 10em;
  padding: 1.25em 0em;
}

@media screen and (max-width: 700px) { 
  .Contact .flex-container {
    flex-direction: column-reverse;
  }

  #contactBox {
    min-width: 100%
  }

  #contactLinks {
    flex-direction: row;
  }

  #emailBox {
    flex-direction: column;
  }

  #contactLinks .buttonWrapper {
    flex-direction: column;
  }
}
.outerFooter {
  background-color: #222222;
}

.Footer {
  text-align: center;
  height: 4em;
  line-height: 4em;
}

.Footer h2 {
  margin: 0;
}

#reactLink:hover #reactIcon {
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

#reactLink:hover #reactIcon {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

@media screen and (max-width: 700px) {
  .Footer {
    height: auto;
    line-height: 2em;
  }
}
